:root {
  --primary: #2fa185;
  --secondary: #e5f1f4;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}
html,
body,
#root {
  height: 100%;
}
.nav {
  background: var(--primary);
  color: #ffffff;
}
.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1180px;
  padding: 16px;
  margin: auto;
  gap: 64px;
}
.brand {
  font-weight: 600;
}
.nav-link-group {
  display: flex;
  align-items: center;
  gap: 32px;
}
.nav-link {
  font-weight: 300;
  font-size: 13px;
  text-decoration: none;
  color: inherit;
}
.nav-link-button {
  border: 1px solid #ffffff;
  padding: 6px 32px;
}
.header {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
.header-title-section {
  flex: 1;
  display: flex;
}
.header-title-section-flex {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 64px;
}
.header-title-container {
  max-width: 550px;
}
.header-title {
  font-size: 64px;
  font-weight: 700;
}
.header-title-style {
  -webkit-text-fill-color: #ffffff;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: var(--primary);
}
.header-desc {
  padding-top: 16px;
  padding-bottom: 32px;
  max-width: 500px;
  opacity: 0.75;
}
.header-search-bar {
  display: flex;
  box-shadow: 0px 8px 24px 0px rgba(133, 133, 133, 0.5);
  border-radius: 3px;
}
.header-search-bar-input-group {
  display: flex;
  align-items: center;
  flex: 1;
}
.header-search-bar-field,
.header-search-bar-dropdown {
  display: flex;
  align-items: center;
  padding: 16px;
}
.header-search-bar-field {
  gap: 8px;
  color: #858585;
}
.header-search-bar-field span {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 75px;
}
.header-search-bar span {
  font-size: 13px;
}
.header-search-bar-divider {
  border-right: 1px solid #858585;
  height: 16px;
}
.header-search-bar-dropdown {
  gap: 16px;
  font-size: 13px;
}
.header-search-bar-button {
  background: var(--primary);
  color: #ffffff;
  padding: 16px 48px;
  font-size: 13px;
  display: flex;
  align-items: center;
}
.header-image {
  flex: 1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header-companies {
  background: var(--secondary);
}
.header-companies-container {
  display: flex;
  align-items: center;
  max-width: 1180px;
  margin: auto;
}
.header-companies-total,
.header-companies-logo-group {
  padding: 56px 48px;
}
.header-companies-total {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.header-companies-total-value {
  font-size: 32px;
  font-weight: 700;
}
.header-companies-total-title {
  font-size: 14px;
  font-weight: 700;
  color: var(--primary);
  white-space: nowrap;
}
.header-companies-divider {
  border-right: 1px solid #858585;
  height: 64px;
}
.header-companies-logo-group {
  flex: 1;
  display: flex;
  justify-content: space-between;
  gap: 32px;
  overflow: auto;
}
.header-companies-logo {
  max-width: 150px;
}
.section-title {
  font-size: 32px;
  font-weight: 700;
}
.popular-subjects-container {
  max-width: 1180px;
  margin: auto;
}
.popular-subjects {
  padding: 128px 16px;
}
.popular-subjects-tab {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 32px 0px;
}
.popular-subjects-tab-button {
  border: 1px solid #272727;
  padding: 8px 16px;
  cursor: pointer;
  text-overflow: ellipsis;
  min-width: 0;
  white-space: nowrap;
  overflow: hidden;
}
.popular-subjects-tab-button.active {
  border: 1px solid var(--primary);
  background: var(--primary);
  color: #ffffff;
}
.popular-subjects-carousel {
  display: flex;
  gap: 16px;
}
.popular-subjects-carousel-item {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 32px;
  gap: 12px;
  background: #f8f8f8;
  border-radius: 4px;
}
.popular-subjects-carousel-item-logo {
  display: flex;
  align-items: center;
  padding: 8px;
  border-radius: 4px;
}
.popular-subjects-carousel-item-title {
  font-weight: 600;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  width: 100%;
  text-align: center;
}
.popular-subjects-carousel-controller {
  display: flex;
  align-items: center;
  gap: 8px;
  justify-content: center;
  padding: 32px 16px;
}
.dot {
  border-radius: 50%;
  height: 8px;
  width: 8px;
  background: #dcdcdc;
  cursor: pointer;
}
.dot.active {
  background: #272727;
}
@media only screen and (max-width: 780px) {
  .nav-link-group {
    display: none;
  }
  .header-title-section {
    display: flex;
    justify-content: center;
  }
  .header-title-section-flex {
    padding: 16px;
  }
  .header-image,
  .header-search-bar,
  .header-companies-divider {
    display: none;
  }
  .header-companies-container {
    margin: 0;
    padding: 16px 0px;
    overflow: auto;
  }
  .popular-subjects-tab {
    width: 100%;
    overflow: auto;
  }
  .header-companies-total,
  .header-companies-logo-group {
    padding: 16px;
  }
  .popular-subjects {
    padding: 64px 16px;
  }
  .popular-subjects-tab-button {
    min-width: unset;
    overflow: unset;
  }
  .popular-subjects-carousel {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    gap: 8px;
  }
  .popular-subjects-carousel-item {
    padding: 16px;
  }
}
@media only screen and (max-width: 600px) {
  .header-companies-container {
    flex-direction: column;
    padding: 16px 0px;
    overflow: unset;
  }
  .header-companies-logo {
    max-width: 70px;
  }
}
